import {ActionType} from "./actions";
import {IGlobalState, IReducer} from "./stateInterfaces";
import testReducer from "./testReducer";
import pageReducer from "./reducers/pageReducer";
import dialogReducer from "./reducers/dialogReducer";

const rootReducer: IReducer = ({test, currentPage, showContactMeDialog}: IGlobalState, action: ActionType) => {
    return {
        test: testReducer(test, action),
        currentPage: pageReducer(currentPage, action),
        showContactMeDialog: dialogReducer(showContactMeDialog, action),
    }
};


export default rootReducer;