// store.js
import React, {createContext, useContext, useReducer} from 'react';
import {IGlobalState, IStateProviderProps} from "./stateInterfaces";
import {ActionType} from "./actions";
import {initialState} from "./initialState";

// const store = createContext(initialState);
// const { Provider } = store;


export const StateContext: React.Context<[IGlobalState, React.Dispatch<ActionType>]> = createContext([
    initialState,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    (value) => {
        /*Nothing*/
    },
]);


export function StateProvider({reducer, initialState, children}: IStateProviderProps) {
    return <StateContext.Provider value={useReducer(reducer, initialState)}>{children}</StateContext.Provider>;
}

export const useGloabalState = () => useContext<[IGlobalState, React.Dispatch<ActionType>]>(StateContext);


