import * as React from "react";
import {useGloabalState} from "../store";
import {ABOUT_ME_PAGE, PROJECTS_PAGE, SHOW_CONTACT_ME_DIALOG} from "../actions";
import {AppBar} from "@material-ui/core";
import makeStyles from "@material-ui/core/styles/makeStyles";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Toolbar from "@material-ui/core/Toolbar";
import GitHubIcon from '@material-ui/icons/GitHub';
import "./rotate.css"
import GitlabIcon from '../images/header/gitlab.png';
import ShipIcon from '../images/header/ship.png'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    buttonPadding: {
    },
    iconPadding: {
        marginRight: "50px"
    },
    menuSpace: {
    },
    navigationSpace: {
        width: "30%",
        display: "flex",
        justifyContent: "space-evenly",
    },
    bar: {
        backgroundColor: '#111'
    }

}));

const Navigation: React.FC = () => {

    const [{currentPage}, dispatch] = useGloabalState();

    const classes = useStyles();

    const openContactMeDialog = () => {
        dispatch({type: SHOW_CONTACT_ME_DIALOG});
    };

    return (
        <div>
            <AppBar className={classes.bar} position="static">
                <Toolbar>
                    <div className={classes.navigationSpace}>
                        <Button color="inherit" onClick={() => {
                            dispatch({type: PROJECTS_PAGE});
                        }}>Projects</Button>
                        <Button color="inherit" onClick={() => {
                            dispatch({type: ABOUT_ME_PAGE})
                        }}>About</Button>
                        {// <Button color="inherit" onClick={openContactMeDialog}>Contact</Button>
                        }
                    </div>

                    <Typography variant="h6" className={classes.title}>
                        <img src={ShipIcon} className="rotate"  width="32" height="32"/>
                    </Typography>

                    <IconButton target="_blank" href="https://github.com/JohnCIT" edge="start" className={classes.iconPadding} color="inherit" aria-label="menu">
                        <GitHubIcon />
                    </IconButton>

                    <IconButton target="_blank" href="https://gitlab.com/gigabottle" edge="start" className={classes.iconPadding} color="inherit" aria-label="menu">
                        <img src={GitlabIcon} height={32} width={32} alt="" />
                    </IconButton>
                </Toolbar>
            </AppBar>
        </div>
    );

};

export default Navigation;