import * as React from "react";
import "../general/rotate.css"
import {Typography} from "@material-ui/core";
import Paper from "@material-ui/core/Paper";

type propsTypes = {
    headTitle: string,
    explainTitle: string,
    googlePlayLink: string,
    images: React.ReactChild
}



const ProjectComponent: React.FC<propsTypes> = (props: propsTypes) => {

    return (
        <div style={{height: "100%", display: "flex", textAlign: "left"}}>
            <Typography style={{color: "white", margin: "30px"}} variant="h6">
                <div style={{backgroundColor: "#191919"}}>
                    <Paper elevation={3} />
                    <div style={{margin: "30px", padding: "20px"}}>
                        {props.headTitle}
                        <br/>
                        {props.explainTitle}
                        <br/>
                        {props.googlePlayLink !== "" ? <a target="_blank" href={props.googlePlayLink}>
                            <img  width="280" height="100" alt='Get it on Google Play' src="https://play.google.com/intl/en_us/badges/static/images/badges/en_badge_web_generic.png"/>
                        </a> : null}
                        <br/>
                        {props.images}
                    </div>
                </div>
            </Typography>
        </div>
    );

};

export default ProjectComponent;