


export type ActionType = | ITestType | ICurrentPage | IDialogState;


export interface ITestType {
    type: typeof TEST_LOAD,
}

export interface ICurrentPage {
    type: PAGE_KEY,
}

export  interface IDialogState {
    type: typeof SHOW_CONTACT_ME_DIALOG | typeof HIDE_CONTACT_ME_DIALOG
}


export type PAGE_KEY  = | typeof ABOUT_ME_PAGE | typeof PROJECTS_PAGE;

export const TEST_LOAD = 'test';

export const ABOUT_ME_PAGE = 'ABOUT_ME_PAGE';

export const PROJECTS_PAGE = 'PROJECTS_PAGE';

export const SHOW_CONTACT_ME_DIALOG = "SHOW_CONTACT_ME_DIALOG";
export const HIDE_CONTACT_ME_DIALOG = "HIDE_CONTACT_ME_DIALOG";