import * as React from "react";
import {useGloabalState} from "../store";
import "../general/rotate.css"
import {Typography} from "@material-ui/core";
import ss1 from '../images/projectImages/ss1.png';
import ss2 from '../images/projectImages/ss2.png';
import Paper from "@material-ui/core/Paper";
import ballb1 from '../images/projectImages/ballb.jpg';
import ballb2 from '../images/projectImages/ballb2.jpg';
import carpark1 from '../images/projectImages/carpark1.png';
import carpark2 from '../images/projectImages/carpark2.png';
import logicbox1 from '../images/projectImages/LogicBox1.png';
import logicbox2 from '../images/projectImages/LogicBox2.png';
import logicbox3 from '../images/projectImages/LogicBox3.png';



import ProjectComponent from "./ProjectComponent";




const ProjectsPage: React.FC = () => {

    const [{currentPage}, dispatch] = useGloabalState();

    let swingerImages: React.ReactChild =
        (
            <>
                <img src={ss1} alt="Game screen shot" width="800" height="500" style={{margin: "30px"}}/>
                <img src={ss2} alt="Game screen shot" width="800" height="500" style={{margin: "30px"}}/>
            </>
        );

    let ballBreakerImages: React.ReactChild =
        (
            <>
                <img src={ballb1} alt="Game screen shot" width="800" height="500" style={{margin: "30px"}}/>
                <img src={ballb2} alt="Game screen shot" width="800" height="500" style={{margin: "30px"}}/>
            </>
        );

    let carParkImages: React.ReactChild =
        (
            <>
                <img src={carpark1} alt="Game screen shot" width="250" height="500" style={{margin: "30px"}}/>
                <img src={carpark2} alt="Game screen shot" width="250" height="500" style={{margin: "30px"}}/>
            </>
        );

    let logicboxSimImages: React.ReactChild =
        (
            <>
                <img src={logicbox1} alt="Logic box screen shot" width="500" height="500" style={{margin: "30px"}}/>
                <img src={logicbox2} alt="Logic box screen shot" width="500" height="500" style={{margin: "30px"}}/>
                <img src={logicbox3} alt="Logic box screen shot" width="500" height="500" style={{margin: "30px"}}/>
            </>
        );

    return (
        <>
            <div style={{display: "flex", textAlign: "left"}}>
                <Typography style={{color: "white", margin: "30px"}} variant="h6">
                    Below is sample of the kind of work I have done on my personal projects.
                </Typography>
            </div>

            <ProjectComponent headTitle={"I make games!"} explainTitle={"Sentient Swinger! A game based on swinging around hand designed levels. Avoid enemies and get to the end of the level."} googlePlayLink={"https://play.google.com/store/apps/details?id=com.Spinky.sentientSwinger&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1"} images={swingerImages} />
            <ProjectComponent headTitle={""} explainTitle={"Ball Breaker! Unlock skins! Avoid bombs. Get points!"} googlePlayLink={"https://play.google.com/store/apps/details?id=com.DefaultCompany.SlingSlicer&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1"} images={ballBreakerImages} />
            <ProjectComponent headTitle={""} explainTitle={"Know where to park before you leave!"} googlePlayLink={"https://play.google.com/store/apps/details?id=com.parking.spinky.parkingspaces"} images={carParkImages} />
            <ProjectComponent headTitle={"Logic box!"} explainTitle={"Logic circuits simulator. Simulate complete circuits such as 8bit adders! Features infinite grid, saving/loading, black boxes and much more!"} googlePlayLink={""} images={logicboxSimImages} />
        </>
    );

};

export default ProjectsPage;