import * as React from "react";
import {useGloabalState} from "../store";
import {Typography} from "@material-ui/core";
import "../general/rotate.css"
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import {HIDE_CONTACT_ME_DIALOG, SHOW_CONTACT_ME_DIALOG} from "../actions";


const ContactMeDialog: React.FC = () => {

    const [{showContactMeDialog}, dispatch] = useGloabalState();

    const hideContactMeDialog = () => {
        dispatch({type: HIDE_CONTACT_ME_DIALOG});
    };

    const onMouseOver = () => {
        return "this.href=this.href.replace('@@','.')";
    };

    return (
        <Dialog onClose={hideContactMeDialog} aria-labelledby="customized-dialog-title" open={showContactMeDialog}>
            <DialogTitle id="customized-dialog-title" >
                Contact Me!
            </DialogTitle>
            <DialogContent dividers>
                <Typography gutterBottom>
                    Feel free to contact me at
                <a href="mailto:user@domain@@com" onMouseOver={onMouseOver}>
                    Send email
                </a>
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button autoFocus onClick={hideContactMeDialog} color="primary">
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ContactMeDialog;