import {ActionType, HIDE_CONTACT_ME_DIALOG, SHOW_CONTACT_ME_DIALOG} from "../actions";


export default function dialogReducer(state: boolean, action: ActionType) {
    switch (action.type) {
        case SHOW_CONTACT_ME_DIALOG:
            return true;
        case HIDE_CONTACT_ME_DIALOG:
            return false;

        default:
            return false;

    }
}