import * as React from "react";
import {useGloabalState} from "../store";
import {Typography} from "@material-ui/core";
import "../general/rotate.css"
import {MainAsteroidView} from "../asteroids/MainAsteroidView";
import Html5Icon from "../images/skills/html5.png";
import CSSIcon from "../images/skills/css.png";
import DockerIcon from "../images/skills/docker.png";
import Git from "../images/skills/git.png";
import Android from "../images/skills/android.png";
import Java from "../images/skills/java.png";
import Jenkins from "../images/skills/jenkins.png";
import ReactSkillIcon from "../images/skills/react.png";
import UnitySkillIcon from "../images/skills/unity.png";
import ContactMeDialog from "./ContactMeDialog";
import makeStyles from "@material-ui/core/styles/makeStyles";


const useStyles = makeStyles((theme) => ({
    imageSpace: {
        margin: "25px"
    }

}));

const AboutMe: React.FC = () => {

    const [{currentPage}, dispatch] = useGloabalState();
    console.log(currentPage);
    const classes = useStyles();

    return (
        <div style={{height: "90%", display: "flex"}}>
            <div style={{width: "50%", marginLeft: "0px"}}>
                <Typography variant="h1" style={{background: "linear-gradient(271deg,#a0e9ff 30%,#a162e8 50%,#f093b0 70%,#edca85 94%)", color: "#fff", WebkitBackgroundClip: "text", WebkitTextFillColor: "transparent"}}>
                    <div >
                        Hi, I'm John!
                        I am a full stack engineer.
                    </div>

                </Typography>
                <Typography style={{color: "white"}} variant="h6">
                    I have completed multiple projects using a wide range of programming languages.

                    I have been working professionally in machine automation and safety using java and web tech stacks.
                    I have completed android apps and unity games which are released and can be found here -- TODO. I have also been a part or multiple freelance projects.
                </Typography>

                <div style={{display: "flex", flexFlow: "wrap", margin: "30px", marginTop: "60px"}}>
                    <img className={classes.imageSpace} src={Html5Icon} height={64} width={64} alt="" />
                    <img className={classes.imageSpace} src={CSSIcon} height={64} width={50} alt="" />
                    <img className={classes.imageSpace} src={DockerIcon} height={64} width={64} alt="" />
                    <img className={classes.imageSpace} src={Git} height={64} width={100} alt="" />
                    <img className={classes.imageSpace} src={Android} height={64} width={120} alt="" />
                    <img className={classes.imageSpace} src={Java} height={64} width={80} alt="" />
                    <img className={classes.imageSpace} src={Jenkins} height={64} width={64} alt="" />
                    <img className={classes.imageSpace} src={ReactSkillIcon} height={64} width={100} alt="" />
                    <img className={classes.imageSpace} src={UnitySkillIcon} height={64} width={64} alt="" />
                </div>

            </div>
            <div style={{width: "50%", marginRight: "0px"}}>
                <MainAsteroidView />
            </div>
            <ContactMeDialog />
        </div>
    );

};

export default AboutMe;