import {ABOUT_ME_PAGE, ActionType, PROJECTS_PAGE} from "../actions";

export const PAGE_ENUM = {
    ABOUT_ME_PAGE: 0,
    PROJECTS_PAGE: 1
};


export default function pageReducer(state: number, action: ActionType) {
    switch (action.type) {
        case ABOUT_ME_PAGE:
            return PAGE_ENUM.ABOUT_ME_PAGE;
        case PROJECTS_PAGE:
            return  PAGE_ENUM.PROJECTS_PAGE;

        default:
            return PAGE_ENUM.ABOUT_ME_PAGE;

    }
}