import React from 'react';
import './App.css';
import {useGloabalState} from "./store";
import AboutMe from "./pages/AboutMe";
import Navigation from "./general/Navigation";
import ProjectsPage from "./pages/ProjectsPage";
import {PAGE_ENUM} from "./reducers/pageReducer";


import * as TestWorker from './WebWorkerDemo';

// Import your worker
// @ts-ignore
import worker from 'workerize-loader!./WebWorkerDemo'; // eslint-disable-line import/no-webpack-loader-syntax


function App() {
  const [{currentPage}, dispatch] = useGloabalState();

    // Create an instance of your worker
    const testWorker = worker();


    testWorker.onmessage = function(oEvent: MessageEvent) {
        console.log('Worker said : ', oEvent.data);
    };

    setInterval(() => {
        testWorker.postMessage('ali');
    }, 10000);


    // Attach an event listener to receive calculations from your worker
    testWorker.addEventListener('message', (message: MessageEvent) => {
        console.log('New Message: ', message.data)
    });
    // Run your calculations
    //testWorker.calculatePrimes(500, 1000000000);


  return (
    <div className="App">
        <Navigation/>
        {currentPage === PAGE_ENUM.ABOUT_ME_PAGE ? <AboutMe/> : <ProjectsPage/>}
    </div>
  );
}

export default App;
